	methods: {
		handleSubmit(data) {
			let addData={};
			addData.snatchMobileNumber=data.snatchMobileNumber;
			addData.rulePublishId=this.rulePublishId;
			if (data.snatchMobileNumber != null && data.snatchMobileNumber != "") {
				uni.request({
					url: this.$serverUrl + '/snatchinfo/insert',
					data: addData,
					method: 'POST',
					success: (res) => {
						uni.showToast({
							title: '上传成功',
							icon: 'success',
							duration: 1000
						});
						uni.navigateBack({ //提交后返回上一个页面
							delta: 1,
						})

					}

				});
				uni.share({
					provider: "weixin",
					scene: "WXSceneSession",
					type: 1,
					summary: this.ruleTitle + this.ruleContent ,
					success: function(res) {
						console.log("success:" + JSON.stringify(res));
					},
					fail: function(err) {
